import React, { useRef, useState } from 'react';
import cookie from 'react-cookies';
import { Link } from 'react-router-dom';


const axios = require('axios');


function Match({ newMatch, bets, size = 'small' }) {
    const [myBet, setMyBet] = useState((bets && bets.hasOwnProperty(newMatch.id)) ? bets[newMatch.id] : null);


    if (myBet === null) {
        if (cookie.load('user')) {
            axios.get(`/api/getPlayerBet/${cookie.load('user').login}/${newMatch.id}`)
                .then((response) => {
                    setMyBet(response.data)
                })
                .catch(function (error) {
                    console.log(error);
                })
        }

    } else {
        console.log(newMatch.id, myBet, myBet.type)
    }
    const inputScoreA = useRef();
    const inputScoreB = useRef();
    function timeConverter(UNIX_timestamp) {
        var a = new Date(UNIX_timestamp);
        var months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
        var year = a.getFullYear();
        var month = months[a.getMonth()];
        var date = a.getDate();

        var hour = (a.getHours() < 10) ? '0' + a.getHours() : a.getHours();
        var mins = (a.getMinutes() < 10) ? '0' + a.getMinutes() : a.getMinutes();
        var time = date + ' ' + month + ' ' + year + ' - ' + hour + ':' + mins;
        return time;
    }
    return (
        <div style={{
            backgroundColor: (myBet && myBet.type) ?
                (myBet.type.indexOf('win') >= 0) ? 'rgba(230, 255, 230, 1)'
                    : (myBet.type.indexOf('full') >= 0) ? 'rgba(230, 250, 255)'
                        : 'rgba(255, 230, 230, 1)'
                : '#FFF',
            minWidth: '200px',
            maxWidth: '245px',
            margin: '5px 10px',
            display: 'flex',
            flexWrap: 'wrap',
            justifyItems: 'end',
            paddingBottom: '10px',
            maxHeight: '120px',
        }} onMouseLeave={() => {
            if (cookie.load('login') && bets) {
                axios.post('../api/sendBet', {
                    scoreA: inputScoreA.current.value,
                    scoreB: inputScoreB.current.value,
                    idMatch: newMatch.id,
                    user: cookie.load('user')
                })
                    .then(function (response) {
                        cookie.save('bets', response.data, { path: '/' });
                    })
                    .catch(function (error) {
                        console.log(error);
                    });
            }
        }} >
            <div style={{
                display: 'inline-flex',
                alignItems: 'center',
                justifyContent: 'space-between',
                width: '100%',
                height: '40px',
                // padding: '5px',
                borderBottom: '2px solid #cc682d',
                color: '#cc682d',
            }}>
                <div style={{
                    marginLeft: '5px'
                }}>

                    {timeConverter(newMatch.start * 1000)}

                </div>
                {newMatch.end ?
                    <div>
                        <Link to={`/match/${newMatch.id}`}>
                            <div style={{
                                background: `#cc682d url('../imgs/stats.png') no-repeat`,
                                width: '40px',
                                height: '40px',
                                backgroundSize: 'contain',
                                backgroundPosition: 'center center',
                            }} />
                        </Link>
                    </div>
                    :
                    (cookie.load('user')) ? <Power user={cookie.load('user').login} match={newMatch} myBet={myBet} setMyBet={setMyBet} />
                        : false}
            </div>
            <div className="teamA" style={{
                display: 'inline-flex',
                alignItems: 'center',
                justifyContent: 'space-between',
                width: '100%',
            }}>
                <div className="icon" style={{
                    minWidth: '40px',
                    minHeight: '40px',
                    background: `url('../imgs/teams/${(newMatch.TeamA !== '???') ? newMatch.TeamA : 'none'}.png') no-repeat`,
                    backgroundSize: 'contain',
                    backgroundPosition: 'center center',
                    marginRight: '5px',
                }} />
                <div className="name" style={{
                    width: '100%',
                    textAlign: 'center',
                }}>
                    {newMatch.TeamA}

                </div>
                {cookie.load('login') && bets ?
                    <ModifScore inputScoreA={inputScoreA} inputScoreB={inputScoreB} match={newMatch} team="A" bets={bets} />
                    : ''}
                <div style={{
                    minWidth: '40px',
                    height: '40px',
                    background: '#cc682d',
                    color: '#FFF',
                    textAlign: 'center',
                    fontSize: '28px',
                }}>
                    {newMatch.scoreA}
                </div>
            </div>
            <div className="teamB" style={{
                display: 'inline-flex',
                alignItems: 'center',
                justifyContent: 'space-between',
                width: '100%',
            }}>
                <div className="icon" style={{
                    minWidth: '40px',
                    minHeight: '40px',
                    background: `url('../imgs/teams/${(newMatch.TeamB !== '???') ? newMatch.TeamB : 'none'}.png') no-repeat`,
                    backgroundSize: 'contain',
                    backgroundPosition: 'center center',
                    marginRight: '5px',
                }} />
                <div className="name" style={{
                    width: '100%',
                    textAlign: 'center',
                }}>
                    {newMatch.TeamB}
                </div>
                <div>
                    {cookie.load('login') && bets ?
                        <ModifScore inputScoreA={inputScoreA} inputScoreB={inputScoreB} match={newMatch} team="B" bets={bets} />
                        : ''}
                </div>
                <div style={{
                    minWidth: '40px',
                    height: '40px',
                    background: '#cc682d',
                    color: '#FFF',
                    textAlign: 'center',
                    fontSize: '28px',
                }}>
                    {newMatch.scoreB}
                </div>
            </div>
        </div>
    )
}

function Power({ user, match, myBet, setMyBet }) {
    const [powers, setPowers] = useState(null)
    const [toogle, setToogle] = useState(false);
    if (powers === null) {
        axios.post('api/getPowersUser', {
            user: user,
        })
            .then(function (response) {
                setPowers(response.data);
            })
            .catch(function (error) {
                console.log(error);
            });
        return (<div className="Power" style={{
            background: `#cc682d center/70% url('../imgs/magic.png') no-repeat`,
            width: '40px',
            height: '40px',
        }}
            onMouseEnter={() => { setPowers(null); setToogle(true); }}
            onMouseLeave={() => { setPowers(null); setToogle(false); }}>
        </div>);

    }
    else {

        const liPower = Object.keys(powers).map((key) => {
            if (powers[key].date.use === 0) {
                return (
                    <div className="spell" key={key} style={{
                        background: `#cc682d center/70% url('./imgs/powers/${powers[key].type}.png') no-repeat`,
                        width: '40px',
                        height: '40px',
                        margin: '2px',
                        transition: '.5s',
                        cursor: 'pointer',
                    }}
                        onMouseEnter={({ currentTarget }) => {
                            currentTarget.style['transform'] = "scale(1.1)";
                        }}
                        onMouseLeave={({ currentTarget }) => {
                            currentTarget.style['transform'] = "scale(1)";
                        }}
                        onClick={() => {
                            setPowers(null);
                            axios.post('api/usePower', {
                                user: user,
                                idMatch: match.id,
                                spell: key,
                            })
                                .then(function (response) {
                                    setToogle(false); setPowers(null); setMyBet(null);
                                })
                                .catch(function (error) {
                                    console.log(error);
                                });


                        }}

                    >
                        {/* {powers[key].name} */}
                    </div>
                )
            }
            return null
        }).filter(x => x)
        return (
            <div
                className="Power"
                style={{
                    background: (myBet && myBet.hasOwnProperty('power')) ? `#cc682d center/70% url('./imgs/powers/${myBet.power.type}.png') no-repeat` : `#cc682d center/70% url('../imgs/magic.png') no-repeat`,
                    width: '40px',
                    height: '40px',
                    position: 'relative'
                }}
                onMouseEnter={() => {
                    if (myBet && !myBet.hasOwnProperty('power')) {
                        setToogle(true)
                    }
                }}
                onMouseLeave={() => {
                    if (myBet && !myBet.hasOwnProperty('power')) {
                        setToogle(false); setPowers(null)
                    }
                }}
                onClick={() => {
                    if (myBet && myBet.hasOwnProperty('power')) {
                        axios.get(`/api/power/remove/${user}/${match.id}`)
                            .then((res) => {
                                console.log(res.data)
                                if (res.data) {
                                    setMyBet(null);
                                }
                            })

                    }
                }}
            >

                <div className='list' style={{
                    background: '#ffffff',
                    width: '200px',
                    maxHeight: '121px',
                    position: 'absolute',
                    top: '0',
                    right: '0',
                    border: '1px solid #cc682d',
                    display: (!toogle) ? 'none' : 'block',
                    overflow: 'scroll'
                }}>
                    <div className="select" style={{
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        flexWrap: 'wrap',
                        padding: '10px',

                    }} >
                        {liPower}
                        {liPower.length === 0 ? "A sec" : false}
                    </div>
                </div>
            </div>
        )
    }



}




function ModifScore({ match, inputScoreA, inputScoreB, team, bets }) {

    function changeCSS(input) {
        input.current.style['transform'] = "scale(1.1)";
        setTimeout(() => {
            input.current.style['transform'] = "scale(1)";
        }, 500)

    }
    if (team === "A") {
        return (
            <div className="bet" style={{
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                minWidth: '100px'
            }}>
                {(Date.now() / 1000 - match.start <= 0 || cookie.load('user').status === 9) ?
                    <div className="lilbtn"
                        style={{
                            borderRadius: '50%',
                            height: '24px',
                            width: '24px',
                            lineHeight: '24px',
                            textAlign: 'center',
                            margin: '5px',
                            border: '2px solid #00000050',
                            color: '#00000050',
                            cursor: 'pointer',
                        }}
                        onMouseEnter={({ currentTarget }) => {
                            currentTarget.style['color'] = '#cc682d';
                            currentTarget.style['border'] = '2px solid #cc682d';
                        }}
                        onMouseLeave={({ currentTarget }) => {
                            currentTarget.style['color'] = '#00000050';
                            currentTarget.style['border'] = '2px solid #00000050';
                        }}

                        onClick={
                            () => {
                                changeCSS(inputScoreA);
                                const score = parseInt(inputScoreA.current.value);
                                (isNaN(parseInt(inputScoreB.current.value))) ? inputScoreB.current.value = 0 : inputScoreB.current.value = parseInt(inputScoreB.current.value);
                                (isNaN(score)) ? inputScoreA.current.value = 1 : (score + 1 > 4) ? inputScoreA.current.value = 4 : inputScoreA.current.value = score + 1;
                            }

                        }
                    >+</div> : false}

                <input
                    type="text"
                    name="TeamA"
                    className="scoreInput"
                    ref={inputScoreA}
                    disabled
                    defaultValue={
                        (bets && bets.hasOwnProperty(match.id)) ?
                            bets[match.id].scoreA :
                            ''
                    }
                    style={{
                        width: '20px',
                        height: '30px',
                        textAlign: 'center',
                        background: '#ffffff',
                        border: 'none',
                        fontSize: '1.2em'
                    }} />

                {(Date.now() / 1000 - match.start <= 0 || cookie.load('user').status === 9) ?
                    <div className="lilbtn"
                        style={{
                            borderRadius: '50%',
                            height: '24px',
                            width: '24px',
                            lineHeight: '24px',
                            textAlign: 'center',
                            margin: '5px',
                            border: '2px solid #00000050',
                            color: '#00000050',
                            cursor: 'pointer',
                        }}
                        onMouseEnter={({ currentTarget }) => {
                            currentTarget.style['color'] = '#cc682d';
                            currentTarget.style['border'] = '2px solid #cc682d';
                        }}
                        onMouseLeave={({ currentTarget }) => {
                            currentTarget.style['color'] = '#00000050';
                            currentTarget.style['border'] = '2px solid #00000050';
                        }}

                        onClick={
                            () => {
                                changeCSS(inputScoreA);
                                const score = parseInt(inputScoreA.current.value);
                                (isNaN(parseInt(inputScoreB.current.value))) ? inputScoreB.current.value = 0 : inputScoreB.current.value = parseInt(inputScoreB.current.value);
                                (isNaN(score) || (score - 1) < 0) ? inputScoreA.current.value = 0 : inputScoreA.current.value = score - 1;
                            }
                        }>-</div> : false}

            </div>
        )
    } else {
        return (
            <div className="bet"
                style={{
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    minWidth: '100px'
                }}>
                {(Date.now() / 1000 - match.start <= 0 || cookie.load('user').status === 9) ? <div className="lilbtn"
                    style={{
                        borderRadius: '50%',
                        height: '24px',
                        width: '24px',
                        lineHeight: '24px',
                        textAlign: 'center',
                        margin: '5px',
                        border: '2px solid #00000050',
                        color: '#00000050',
                        cursor: 'pointer',
                    }}
                    onMouseEnter={({ currentTarget }) => {
                        currentTarget.style['color'] = '#cc682d';
                        currentTarget.style['border'] = '2px solid #cc682d';
                    }}
                    onMouseLeave={({ currentTarget }) => {
                        currentTarget.style['color'] = '#00000050';
                        currentTarget.style['border'] = '2px solid #00000050';
                    }}

                    onClick={
                        () => {
                            changeCSS(inputScoreB);
                            const score = parseInt(inputScoreB.current.value);
                            (isNaN(parseInt(inputScoreA.current.value))) ? inputScoreA.current.value = 0 : inputScoreA.current.value = parseInt(inputScoreA.current.value);
                            (isNaN(score)) ? inputScoreB.current.value = 1 : (score + 1 > 4) ? inputScoreB.current.value = 4 : inputScoreB.current.value = score + 1;
                        }
                    }>+</div> : false}

                <input
                    type="text"
                    name="TeamB"
                    className="scoreInput"
                    ref={inputScoreB}
                    disabled
                    defaultValue={
                        (bets && bets.hasOwnProperty(match.id)) ?
                            bets[match.id].scoreB :
                            ''
                    }
                    style={{
                        width: '20px',
                        height: '30px',
                        textAlign: 'center',
                        background: '#ffffff',
                        border: 'none',
                        fontSize: '1.2em'
                    }} />

                {(Date.now() / 1000 - match.start <= 0 || cookie.load('user').status === 9) ?
                    <div className="lilbtn"
                        style={{
                            borderRadius: '50%',
                            height: '24px',
                            width: '24px',
                            lineHeight: '24px',
                            textAlign: 'center',
                            margin: '5px',
                            border: '2px solid #00000050',
                            color: '#00000050',
                            cursor: 'pointer',
                        }}
                        onMouseEnter={({ currentTarget }) => {
                            currentTarget.style['color'] = '#cc682d';
                            currentTarget.style['border'] = '2px solid #cc682d';
                        }}
                        onMouseLeave={({ currentTarget }) => {
                            currentTarget.style['color'] = '#00000050';
                            currentTarget.style['border'] = '2px solid #00000050';
                        }}

                        onClick={
                            () => {
                                changeCSS(inputScoreB);
                                const score = parseInt(inputScoreB.current.value);
                                (isNaN(parseInt(inputScoreA.current.value))) ? inputScoreA.current.value = 0 : inputScoreA.current.value = parseInt(inputScoreA.current.value);
                                (isNaN(score) || (score - 1) < 0) ? inputScoreB.current.value = 0 : inputScoreB.current.value = score - 1;
                            }
                        }
                    >-</div>
                    : false}

            </div>
        )
    }
}


export default Match;