function Player({ player }) {

    const [infoPlayer, setInfoPlayer] = useState(null);
    const [urlTwitch, setUrlTwitch] = useState(null);
    const [hover, setHover] = useState(false)

    if (infoPlayer == null) {
        axios.get(`https://api.overwatchleague.com/players/${player.id}`)
            .then(function (response) {
                setInfoPlayer(response.data.data.player);
            })
            .catch(function (error) {
                console.log(error);
            });


    }

    if (infoPlayer !== null && urlTwitch === null) {
        setUrlTwitch(infoPlayer.accounts.find((account) => {
            return account.accountType === "TWITCH";
        }))
    }


    const Heros = (player.attributes.heroes) ? player.attributes.heroes.map((hero) => {
        return (<div className={`ohi ${hero.name}`} key={hero.name}></div>)
    }) : null;

    return (
        <div
            style={{
                background: 'red',
                height: '200px',
                width: '200px',
                margin: '1px',
                background: ` bottom 20px center/90% url("${player.headshot}") no-repeat`,
                border: '2px solid #fff',
                position: 'relative',
                cursor: 'pointer',
            }}
            onMouseEnter={({ currentTarget }) => {
                // currentTarget.style['background'] = `#cc682d bottom 20px center/90% url("${player.headshot}") no-repeat`
                currentTarget.style['border'] = "2px solid #cc682d";
                currentTarget.querySelector('.name').style['color'] = '#fff';
                currentTarget.querySelector('.name').style['background'] = '#cc862d';
            }}
            onMouseLeave={({ currentTarget }) => {
                // currentTarget.style['background'] = `#fff bottom 20px center/90% url("${player.headshot}") no-repeat`
                currentTarget.style['border'] = "2px solid #fff";
                currentTarget.querySelector('.name').style['color'] = '#cc682d';
                currentTarget.querySelector('.name').style['background'] = '#fff';

            }}
        >
            <div className="name" style={{
                width: '100%',
                color: '#cc682d',
                background: '#fff',
                textAlign: 'center',
                bottom: '0',
                position: 'absolute',
                borderTop: '2px solid #cc682d',
            }}>
                {player.givenName} "{player.name}" {player.familyName}

            </div>
            <div className="picks">
                {
                    (Heros && Heros.length > 0) ?
                        Heros.slice(0, 5)
                        : false
                }
            </div>
            {
                (urlTwitch !== null) ?
                    <TwitchWidget urlTwitch={urlTwitch} name={player.name}></TwitchWidget>
                    : false
            }
        </div>
    )

}
function TwitchWidget({ urlTwitch, name }) {

    const idTwitch = (urlTwitch !== undefined) ? urlTwitch.value.split('/').pop() : null;

    const [online, setOnline] = useState(false);

    if (!online && urlTwitch !== undefined) {
        // `https://api.twitch.tv/kraken/streams/${idTwitch}?client_id=${keyTwitch}`
        axios.get(`https://api.twitch.tv/kraken/streams/${idTwitch}?client_id=${keyTwitch}`)
            .then(function (response) {
                if (response.data.stream != null) {
                    setOnline(response.data.stream);
                }

            })
            .catch(function (error) {
                console.log(error);
            });
    }
    return (
        <div>

            {(online.game) ?
                <a href={urlTwitch.value} target="_blank" rel="noopener noreferrer">
                    <div style={{
                        position: 'absolute',
                        right: '0',
                        top: '0',
                        height: '50px',
                        width: '50px',
                        background: '#cc682d center/70% url("../imgs/twitch.png") no-repeat',
                        color: '#ffffff',
                    }}>
                    </div>
                </a>
                : false}
        </div>
    )


}



export default withRouter(Team);