import React, { useRef, useState } from 'react';
import { Link, withRouter } from 'react-router-dom';
import cookie from 'react-cookies';

import Modal from '../../components/Modal';



const axios = require('axios');


function Login({ history }) {

    window.addEventListener('keypress', function (e) {
        if (e.keyCode === 13) {
            sendLogin()
        }
    }, false);



    const [modal, setModal] = useState({
        show: false,
        message: ""
    });
    function toggleModal(clean = false) {
        setModal({
            show: !modal.show,
            message: (clean) ? modal.message : ""
        })
    }
    function sendLogin() {
        setModal({
            show: true,
            message: "Tu va etre redirigé attend un peu"
        });
        axios.post('api/login', {
            login: login.current.value,
            pass: password.current.value
        })
            .then(function (response) {
                if (response.data.status) {
                    cookie.save('bets', response.data.data.bets, { path: '/' });
                    delete response.data.data.mdp;
                    // delete response.data.data.mail;
                    response.data.data.bets = "";
                    cookie.save('login', true, { path: '/' });
                    cookie.save('user', response.data.data, { path: '/' });


                    history.push('/')
                    // setModal({
                    //     show: true,
                    //     message: response.data.message
                    // });
                    // setTimeout(() => {
                    //     setModal({
                    //         show: false,
                    //         message: ""
                    //     });
                    // }, 3000)
                } else {
                    setModal({
                        show: true,
                        message: response.data.message
                    });
                }
                setTimeout(() => {
                    setModal({
                        show: false,
                        message: ''
                    });
                }, 2500)
            })
            .catch(function (error) {
                console.log(error);
            });

    }

    const login = useRef();
    const password = useRef();

    return (
        <div style={{
            marginTop: '50px',
        }}>
            <Modal modal={modal} toggleModal={toggleModal}></Modal>
            <div className="loginFrom" style={{
                padding: '0 1vw'
            }}>
                <div style={{
                    textAlign: 'center',
                }}>
                    <label htmlFor="login" style={{
                        width: '100%',
                        textAlign: 'center',
                        display: 'block',
                    }}>Pseudo :</label>
                    <input type="text" name="login" id="login" ref={login} style={{
                        maxWidth: '250px',
                        height: '30px',
                        fontSize: '25px',
                        lineHeight: '28px',
                        padding: '5px 10px',
                        background: '#ffffff',
                        border: 'none',
                        borderBottom: '2px solid #cc682d',
                        outline: 'none',
                        margin: '1vh 0 3vh 0',
                    }} />
                </div>
                <div style={{
                    textAlign: 'center',
                }}>
                    <label htmlFor="password" style={{
                        width: '100%',
                        textAlign: 'center',
                        display: 'block',
                    }}>Password :</label>
                    <input type="password" name="password" id="password" ref={password} style={{
                        maxWidth: '250px',
                        height: '30px',
                        fontSize: '25px',
                        lineHeight: '28px',
                        padding: '5px 10px',
                        background: '#ffffff',
                        border: 'none',
                        borderBottom: '2px solid #cc682d',
                        outline: 'none',
                        margin: '1vh 0 3vh 0',

                    }} />
                </div>
                <div style={{
                    textAlign: 'center',
                    display: 'block',
                    alignItems: 'center',
                    justifyContent: 'center',
                }}>

                    <div onClick={sendLogin} style={{
                        background: '#cc682d',
                        color: '#ffffff',
                        width: '100px',
                        textAlign: 'center',
                        margin: '1vh auto',
                        padding: '5px 10px',
                        cursor: 'pointer',
                        fontSize: '1.2em',
                    }} onMouseEnter={({ currentTarget }) => {
                        currentTarget.style['transform'] = 'scale(1.1)'
                    }} onMouseLeave={({ currentTarget }) => {
                        currentTarget.style['transform'] = 'scale(1)'
                    }}>Validez</div>
                    <Link to="/signIn">
                        <div style={{
                            background: '#cc682d',
                            color: '#ffffff',
                            width: '100px',
                            textAlign: 'center',
                            margin: '1vh auto',
                            padding: '5px 10px',
                            cursor: 'pointer',
                            fontSize: '1.2em'
                        }} onMouseEnter={({ currentTarget }) => {
                            currentTarget.style['transform'] = 'scale(1.1)'
                        }} onMouseLeave={({ currentTarget }) => {
                            currentTarget.style['transform'] = 'scale(1)'
                        }}>S'inscrire</div>
                    </Link>
                </div>
            </div>
        </div>
    )
}



export default withRouter(Login);