function findMapName(guid) {
    // console.log(MAP);
    // console.log(guid);
    const result = MAP.find((map) => {
        return map.guid === guid;
    }) || { name: { fr_FR: "LOL" }, id: "lol" };
    // console.log(result);
    return {
        name: result.name.fr_FR,
        tag: result.id,
    }
}


