import React, { useState } from 'react';
import './assets/css/App.css';

import { BrowserRouter as Router, Route } from "react-router-dom";

import Nav from "./components/Nav";
import Konami from "./components/Konami";
// import TimerMatch from "./components/TimerMatch";

import Weeks from "./pages/Weeks";
import Teams from "./pages/Teams";
import Login from "./pages/Login";
import SignIn from "./pages/SignIn";
import Team from "./pages/Team";
import Home from "./pages/Home";
import LeaderBoard from "./pages/LeaderBoard";
import Profil from "./pages/Profil";
import EditProfil from "./pages/EditProfil";
import Rules from "./pages/Rules";
import Match from "./pages/Match";



const axios = require('axios');


function App() {
  const [Stages, setStages] = useState(null);

  if (Stages === null) {

    axios.get('../api/Stages')
      .then(function (response) {
        setStages(response.data);
      })
      .catch(function (error) {
        console.log(error);
      });
    return (
      <div className="App">
      </div>
    );

  }

  const [currentStage, setCurrentStage] = useState(
    Stages.map((stage, key) => {
      return (Date.now() / 1000 < stage.end) ? key : false;
    }).filter(x => x).shift()

  );



  return (
    <Router>
      <div className="App" style={{
        color: '#cc682d',
        fontFamily: 'OWfont, serif',
        background: `url('../imgs/back.jpg')`,
        backgroundAttachment: 'fixed',
        backgroundSize: 'cover',
        minWidth: '100vw',
        minHeight: '100vh',
        padding: '0 auto 0 auto',
        display: 'block',
        alignItems: 'start',
        justifyContent: 'center',
        margin: '0',
      }}>
        <header style={{
          // paddingLeft: '10vw',
          // minHeight: '100px',
          // paddingTop: '100px',

        }}>
          <h1 style={{
            fontSize: '2.4em',
            textAlign: 'center',
            margin: '0',
            padding: '2vh 0'
          }}><span style={{
            fontSize: '1.2em',
            color: '#FFF'
          }}>O</span>ver<span style={{
            fontSize: '1.2em',
            color: '#FFF'
          }}>B</span>ets</h1>
        </header>
        <div className="body" style={{
          background: '#fff',
          maxWidth: '50vw',
          minWidth: '310px',
          margin: '0 auto',
          paddingBottom: '100px',

        }}>

          <Nav />
          {/* <Konami /> */}
          {/* <TimerMatch stages={shedule.data.stages} /> */}

          <div className="main">

            <Route exact path="/" render={() =>
              <Home></Home>
            } />
            <Route path="/matches" render={
              () => {
                return <Weeks
                  Stages={Stages}
                  setCurrentStage={setCurrentStage}
                  currentStage={currentStage}
                />
              }
            } />
            <Route path="/teams" render={() => <Teams></Teams>} />
            <Route path="/login" render={() => <Login></Login>} />
            <Route path="/signIn" render={() => <SignIn></SignIn>} />
            <Route path="/team/:idteam" render={() => <Team></Team>} />
            <Route path="/match/:idMatch" render={() => <Match></Match>} />
            <Route path="/leaderBoard" render={() => <LeaderBoard></LeaderBoard>} />
            <Route path="/stream" render={() => <h2>STREAM</h2>} />
            <Route path="/profil/:user" render={() => <Profil></Profil>} />
            <Route path="/myProfil" render={() => <EditProfil></EditProfil>} />
            {/* <Route path="/chat" render={() => <Chat></Chat>} /> */}
            <Route path="/rules" render={() => <Rules></Rules>} />

          </div>

        </div>
      </div>
    </Router>
  );
}




export default App;
