import React, { useState } from 'react';
import { withRouter } from 'react-router-dom';
import cookie from 'react-cookies';

import Switch from "react-switch";
import Modal from '../../components/Modal';
import Achievement from '../../components/Achievement';
import UserBets from '../../components/UserBets';

const axios = require('axios');

function EditProfil({ history }) {


    const [modal, setModal] = useState({
        show: false,
        message: ""
    });
    function toggleModal(clean = false) {
        setModal({
            show: !modal.show,
            message: (clean) ? modal.message : ""
        })
    }

    function sendProfil() {
        setModal({
            show: true,
            message: ''
        });
        axios.post('../api/updatePlayer', {
            user: user,
        })
            .then(function (response) {
                setModal({
                    show: true,
                    message: response.data.message
                });
                if (response.data.redirect) {

                    setTimeout(() => {
                        cookie.remove('login');
                        cookie.remove('user');
                        cookie.remove('bets');
                        history.push('/')

                    }, 3000)
                }
                setTimeout(() => {
                    setModal({
                        show: false,
                        message: ''
                    });
                }, 4500)
            })
            .catch(function (error) {
                console.log(error);
            });

    }
    const [userName] = useState(cookie.load('user').login);
    const [user, setUser] = useState(null);

    if (user != null) {
        return (
            <div className="Profil">
                <Modal modal={modal} toggleModal={toggleModal}></Modal>
                <h2>Mon Profil</h2>
                <div className="user" style={{
                    display: 'flex',
                    flexWrap: 'wrap',
                    justifyContent: 'center',
                    alignItems: 'center',
                }}>

                    <div className="name" style={{
                        margin : '0 auto',
                        maxWidth: '300px',
                        minWidth: '240px',

                    }}>
                        <div style={{
                            width: '200px',
                        }}>Pseudo :</div>
                            <input type="text" id="pseudo" defaultValue={user.name} onBlur={(event) => {
                                user.name = (event.target.value) ? event.target.value : user.name;
                                cookie.save('user', user, { path: '/' });
                                setUser(user);
                            }} />
                            <div className="alert">
                                Ton pseudo n'est pas ton login
                            </div>
                        
                    </div>
                    <div className="password" style={{
                        margin : '0 auto',
                        maxWidth: '300px',
                        minWidth: '240px',

                    }}>
                        <div  style={{
                            width: '200px',
                        }}>Password :</div>
                            <input type="password" id="password" placeholder="*********" onBlur={(event) => {
                                user.mdp = (event.target.value) ? event.target.value : user.mdp;
                                cookie.save('user', user, { path: '/' });
                                setUser(user);
                            }} />
                            <div className="alert">
                                Si tu change ton password, tu sera deconnecté
                                </div>
                        

                    </div>
                    <div className="email" style={{
                        margin : '0 auto',
                        maxWidth: '300px',
                        minWidth: '240px',

                    }}>
                        <div htmlFor="email">Email :</div>
                            <input type="text" id="email" defaultValue={user.mail} onBlur={(event) => {
                                user.mail = (event.target.value) ? event.target.value : user.mail;
                                cookie.save('user', user, { path: '/' });
                                setUser(user);
                            }} />
                            <div className="alert">
                                Soit avertie de l'avancé du projet
                                </div>
                        
                    </div>
                    <div className="btag" style={{
                        margin : '0 auto',
                        maxWidth: '300px',
                        minWidth: '240px',

                    }}>
                        <div htmlFor="btag">Btag :</div>
                            <input type="text" id="btag" defaultValue={user.btag} onBlur={(event) => {
                                user.btag = (event.target.value) ? event.target.value : user.btag;
                                cookie.save('user', user, { path: '/' });
                                setUser(user);
                            }} />
                            <div className="alert">
                                Sans btag pas de recompense possible
                                </div>
                        
                    </div>
                    <div className="visibility" style={{
                        margin : '0 auto',
                        maxWidth: '300px',
                        minWidth: '240px',

                    }}>
                        Profil Public : (A venir)<br />
                        <Switch
                            onChange={() => {
                                user.visibility = (user.visibility) ? !user.visibility : true;
                                cookie.save('user', user, { path: '/' });
                                setUser(user);
                            }}
                            checked={(user.visibility) ? user.visibility : false}
                            onColor={'#cc682d'}
                            activeBoxShadow={'0 0 2px 3px #cc682d'}
                        ></Switch>

                    </div>
                    {/* <div>IMG (A venir)</div> */}
                    <div className="loveTeam" style={{
                        margin : '0 auto',
                        maxWidth: '300px',
                        minWidth: '240px',

                    }}>
                        LoveTeam :

                            <select>
                            <option value={user.loveTeam}>{user.loveTeam} </option>
                        </select>
                    </div>
                    <div>

                    </div>
                    <div className="submit" style={{
                        margin : '0 auto',
                        maxWidth: '300px',
                        minWidth: '240px',

                    }}>
                        <div className="btn button" onClick={sendProfil}>
                            Valider
                        </div>
                    </div>

                </div>
                <h2>Haut Fait</h2>
                {/* <Achievement user={user} /> */}
                <h2>Pronostic</h2>
                {/* {(user.bets) ? <UserBets user={user} /> : ''} */}
            </div>
        )
    }
    else if (userName != null) {
        axios.get(`../api/getPlayer/${userName}`)
            .then(function (response) {
                setUser(response.data);
            })
            .catch(function (error) {
                console.log(error);
            });
        return (<div></div>)
    }
    else {
        return (<div></div>)
    }






}



export default withRouter(EditProfil);