import React, { useState } from 'react';
import { withRouter } from 'react-router-dom';
import cookie from 'react-cookies';



import Match from "../../components/Match";

const axios = require('axios');

function Weeks({ Stages, currentStage, setCurrentStage }) {
    const arrayMatches = Stages[currentStage].matches.map((match) => {
        return {
            id: match.subEvents.id || 0,
            start: match.start,
        }
    }).filter((item) => item);

    const arrayWeeks = Array.from(new Set(
        arrayMatches.map(x => x.id)
    )).map(id => {
        return arrayMatches.find(s => {
            return s.id === id
        })
    });

    let findWeek = arrayWeeks.find((week) => {
        return week.start < Date.now() / 1000
    }) || arrayWeeks[0];


    const [currentWeek, setCurrentWeek] = useState(findWeek.id);



    const arrayCurrentWeek = Stages[currentStage].matches.map((match) => {
        if (match.subEvents.id === currentWeek) {
            return match;
        }
        return null
    }).filter((item) => item);


    const [bets, setBets] = useState((cookie.load('bets')) ? cookie.load('bets') : false)
    if (cookie.load('login') && bets === false) {
        axios.get(`../api/getPlayer/${cookie.load('user').login}`)
            .then(function (response) {
                setBets(response.data.bets)
            })
            .catch(function (error) {
                console.log(error);
            });
    }

    return (
        <div>
            <div
                style={{
                    display: 'inline-flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    marginBottom: '30px',
                    width: '100%',
                }}>

                <div
                    style={{
                        display: 'inline-flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        margin: '0 20px',
                        background: '#cc682d',
                        color: '#fff'
                    }}
                >
                    <div
                        style={{
                            background: '#FFF',
                            // borderRadius: '50%',
                            textAlign: 'center',
                            height: '40px',
                            width: '40px',
                            fontSize: '30px',
                            lineHeight: '34px',
                            color: '#000',
                            cursor: 'pointer',
                            border: '3px solid #FFF',
                            margin: '5px',
                        }}
                        onMouseEnter={(e) => {
                            e.currentTarget.style['background'] = '#FFFFFF40';
                            e.currentTarget.style['border'] = '3px solid #FFF';
                            e.currentTarget.style['color'] = '#FFF';
                        }} onMouseLeave={(e) => {
                            e.currentTarget.style['background'] = '#FFFFFF';
                            e.currentTarget.style['border'] = '3px solid #FFF';
                            e.currentTarget.style['color'] = '#000';
                        }}
                        onClick={() => {
                            setCurrentStage((currentStage - 1 >= 0) ? currentStage - 1 : Stages.length - 1)
                        }}
                    >
                        {"<"}
                    </div>
                    <div style={{
                        padding: '0 10px'
                    }}>

                        {Stages[currentStage].name}
                    </div>
                    <div
                        style={{
                            background: '#FFF',
                            // borderRadius: '50%',
                            textAlign: 'center',
                            height: '40px',
                            width: '40px',
                            fontSize: '30px',
                            lineHeight: '34px',
                            color: '#000',
                            cursor: 'pointer',
                            border: '3px solid #FFF',
                            margin: '5px',
                        }}
                        onMouseEnter={(e) => {
                            e.currentTarget.style['background'] = '#FFFFFF40';
                            e.currentTarget.style['border'] = '3px solid #FFF';
                            e.currentTarget.style['color'] = '#FFF';
                        }} onMouseLeave={(e) => {
                            e.currentTarget.style['background'] = '#FFFFFF';
                            e.currentTarget.style['border'] = '3px solid #FFF';
                            e.currentTarget.style['color'] = '#000';
                        }}
                        onClick={() => {
                            setCurrentStage((currentStage + 1 < Stages.length) ? currentStage + 1 : 0)
                        }}
                    >
                        {">"}
                    </div>
                </div>
                <div
                    style={{
                        display: 'inline-flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        margin: '0 20px',
                        background: '#cc682d',
                        color: '#fff'
                    }}>
                    <div
                        style={{
                            background: '#FFF',
                            // borderRadius: '50%',
                            textAlign: 'center',
                            height: '40px',
                            width: '40px',
                            fontSize: '30px',
                            lineHeight: '34px',
                            color: '#000',
                            cursor: 'pointer',
                            border: '3px solid #FFF',
                            margin: '5px',
                        }}
                        onMouseEnter={(e) => {
                            e.currentTarget.style['background'] = '#FFFFFF40';
                            e.currentTarget.style['border'] = '3px solid #FFF';
                            e.currentTarget.style['color'] = '#FFF';
                        }} onMouseLeave={(e) => {
                            e.currentTarget.style['background'] = '#FFFFFF';
                            e.currentTarget.style['border'] = '3px solid #FFF';
                            e.currentTarget.style['color'] = '#000';
                        }}
                        onClick={() => {
                            setCurrentWeek((currentWeek - 1 < 0) ? arrayWeeks.length - 1 : currentWeek - 1);
                        }}
                    >
                        {"<"}
                    </div>
                    <div style={{
                        padding: '0 10px'
                    }}>
                        {(arrayCurrentWeek[0]) ? arrayCurrentWeek[0].subEvents.name : null}
                    </div>
                    <div
                        style={{
                            background: '#FFF',
                            // borderRadius: '50%',
                            textAlign: 'center',
                            height: '40px',
                            width: '40px',
                            fontSize: '30px',
                            lineHeight: '34px',
                            color: '#000',
                            cursor: 'pointer',
                            border: '3px solid #FFF',
                            margin: '5px',
                        }}
                        onMouseEnter={(e) => {
                            e.currentTarget.style['background'] = '#FFFFFF40';
                            e.currentTarget.style['border'] = '3px solid #FFF';
                            e.currentTarget.style['color'] = '#FFF';
                        }} onMouseLeave={(e) => {
                            e.currentTarget.style['background'] = '#FFFFFF';
                            e.currentTarget.style['border'] = '3px solid #FFF';
                            e.currentTarget.style['color'] = '#000';
                        }}
                        onClick={() => {
                            setCurrentWeek((currentWeek + 1 > arrayWeeks.length - 1) ? 0 : currentWeek + 1);
                        }}
                    >
                        {">"}
                    </div>
                </div>





            </div>
            <div className="Weeks">
                <Week bets={bets} arrayCurrentWeek={arrayCurrentWeek} />
            </div>
        </div>


    );

}
function Week({ bets, arrayCurrentWeek }) {
    const Matchs = arrayCurrentWeek.map(match => {
        return <Match newMatch={match} key={match.id} bets={bets} size='large' />
    })
    return (
        <div className="Matchs" style={{
            display: 'flex',
            flexWrap: 'wrap',
            alignItems: 'center',
            justifyContent: 'center'
        }}>
            {Matchs}
        </div>

    )
}


export default withRouter(Weeks);