import React from 'react';
import { Link, withRouter } from "react-router-dom";
import cookie from 'react-cookies'

function Nav({ history }) {


    const Menu = [
        {
            name: 'Home',
            route: '/',
            loged: false
        },
        {
            name: 'Rules',
            route: '/rules',
            loged: false
        },
        {
            name: 'Matches',
            route: '/matches',
            loged: false
        },
        {
            name: 'Teams',
            route: '/teams',
            loged: false
        },
        {
            name: 'Classements',
            route: '/leaderBoard',
            loged: true
        },
        {
            name: 'Profil',
            route: '/myProfil',
            loged: true
        },
        {
            name: 'Login',
            route: '/login',
            loged: false
        },
        {
            name: 'Logout',
            route: '/logout',
            loged: true
        },

    ].map((link, key) => {
        if (link.loged === true && cookie.load('login') && link.name !== "Logout" && link.name !== "Login") {

            return (
                <div key={key} style={{
                    margin: '0 10px',
                    fontSize: '1.4em',
                    color: '#ffffff',

                }} onMouseEnter={({ currentTarget }) => {
                    currentTarget.style['transform'] = 'scale(1.2)';
                }} onMouseLeave={({ currentTarget }) => {
                    currentTarget.style['transform'] = 'scale(1)';
                }}>
                    <Link to={link.route} style={{
                        color: '#ffffff',
                        //   borderBottom: '2px solid #ffffff',
                    }}>{link.name}</Link>
                </div>
            )

        }
        if (link.name === "Login" && !cookie.load('login')) {
            return (
                <div key={key} style={{
                    margin: '0 10px',
                    fontSize: '1.4em',
                    color: '#ffffff',

                }} onMouseEnter={({ currentTarget }) => {
                    currentTarget.style['transform'] = 'scale(1.2)';
                }} onMouseLeave={({ currentTarget }) => {
                    currentTarget.style['transform'] = 'scale(1)';
                }}>
                    <Link to={link.route} style={{
                        color: '#ffffff',
                        //   borderBottom: '2px solid #ffffff',
                    }}>{link.name}</Link>
                </div>
            )
        }

        if (link.name === "Logout" && cookie.load('login')) {
            return (
                <div key={key} style={{
                    margin: '0 10px',
                    fontSize: '1.4em',
                    color: '#ffffff',
                    cursor: 'pointer',
                    textDecoration: 'underline',
                }} onMouseEnter={({ currentTarget }) => {
                    currentTarget.style['transform'] = 'scale(1.2)';
                    currentTarget.style['borderBottom'] = '2px solid #ffffff !important';

                }} onMouseLeave={({ currentTarget }) => {
                    currentTarget.style['transform'] = 'scale(1)';
                    currentTarget.style['borderBottom'] = 'none';
                }} onClick={() => {
                    cookie.remove('login');
                    cookie.remove('user');
                    cookie.remove('bets');
                    history.go('/');
                }}>
                    {link.name}
                </div>
            )
        }
        if (link.loged === false && link.name !== "Login") {
            return (
                <div key={key} style={{
                    margin: '0 10px',
                    fontSize: '1.4em',
                    color: '#ffffff',

                }} onMouseEnter={({ currentTarget }) => {
                    currentTarget.style['transform'] = 'scale(1.2)';
                }} onMouseLeave={({ currentTarget }) => {
                    currentTarget.style['transform'] = 'scale(1)';
                }}>
                    <Link to={link.route} style={{
                        color: '#ffffff',
                        //   borderBottom: '2px solid #ffffff',
                    }}>{link.name}</Link>
                </div>
            )
        }
return false;



    });


    return (
        <nav style={{
            // marginTop: '40px',
            display: 'inline-flex',
            listStyle: 'none',
            alignItems: 'center',
            justifyContent: 'center',
            flexWrap: 'wrap',
            height: '4em',
            background: '#cc682d',
            // color: '#cc682d',
            width: '50vw',
            minWidth: '310px',
            position: 'relative',
        }}>
            {Menu}
        </nav>
    )


}


export default withRouter(Nav);