import React from 'react';

function Modal({ modal, toggleModal }) {
    return (
        <div className={"modal"}
            style={{
                display: (modal.show) ? 'block' : 'none',
                // display: 'block',

            }}>
            <div className="body" style={{
                background: '#ffffff',
                position: 'relative',
                minHeight: '20px',
                margin: '0 5vw',
                border: '1px solid #cc682d'
            }}>
                <div className="close" style={{
                    position: 'absolute',
                    right: '0',
                    background: '#ffffff',
                    height: '20px',
                    width: '20px',
                    textAlign: 'center',
                }} onClick={
                    () => {
                        toggleModal(true);
                    }
                }>✘</div>
                <div className="message" style={{
                    padding: '10px 20px',
                    textAlign: 'center'
                }}>
                    {modal.message}
                </div>
            </div>
        </div>
    );
}
export default Modal;