import React, { useState } from 'react';

const axios = require('axios');

function SpellBonus({ typeBonus }) {

    const [toggle, setToggle] = useState(false);

    const [Powers, setPowers] = useState(null);

    if (Powers === null) {
        axios.get('/api/power').then((res) => {
            console.log(res.data);
            setPowers(res.data);
        })
    }

    if (Powers !== null) {
        const currentPower = Powers.find((power) => power.type === typeBonus);


        return (
            <div
                onMouseEnter={({ currentTarget }) => {
                    currentTarget.style['transform'] = "scale(1.1)";
                }}
                onMouseLeave={({ currentTarget }) => {
                    currentTarget.style['transform'] = "scale(1)";
                    setToggle(false)
                }}
                onClick={() => {
                    setToggle(!toggle)
                }}
            >
                <div style={{
                    background: `${(currentPower.valid)?'#cc682d':'#E3E3E3'} center/70% url("./imgs/powers/${currentPower.img}") no-repeat`,
                    color: '#ffffff',
                    height: '50px',
                    width: '50px',
                    position: 'relative',
                    margin: '2px',
                }} />
                <div style={{
                    display: (toggle) ? 'block' : 'none',
                    background: '#ffffff',
                    width: '250px',
                    // height: '250px',
                    position: 'absolute',
                    border: '1px solid #cc682d',
                    top: '50px',
                    right: '-100px',
                    zIndex: '1000',
                    padding: '10px',
                }}>
                    <div style={{
                        fontSize: '1.4em',
                        textAlign: 'center',
                        width: '100%',

                    }}>
                        "{currentPower.name}"
                    </div>
                    <div style={{
                        fontSize: '0.9em',
                        color: '#000000',
                        fontFamily: 'Roboto, sans-serif',
                        textAlign: 'justify',
                    }}>
                        {currentPower.longText}
                    </div>

                </div>
            </div>
        )
    }
    return (
        <div></div>
    )

}

export default SpellBonus;