import React, { useState } from 'react';

const axios = require('axios');

function LoveTeam({ user, team }) {
    // console.log(user);

    const [love, setLove] = useState(null);

    function sendLoveTeam() {
        axios.post('../api/loveTeam', {
            teamId: team,
            user: user
        })
            .then(function (response) {
                // console.log(cookie.load('user'));
                // cookie.remove('user', { path: '/' });
                // console.log(cookie.load('user'));
                console.log(response.data)
                setLove(null);
                // cookie.save('user', response.data, { path: '/' });

                // console.log(cookie.load('user'));
                // history.push(history.location.pathname);
                // setTeam(null);

            })
            .catch(function (error) {
                console.log(error);
            });
    }

    if (love === null && user !== false) {
        axios.get(`/api/getPlayer/${user}`)
            .then(function (response) {
                // console.log(response.data.loveTeam);
                setLove(response.data.loveTeam);

            });
    }
    let status = 'none';
    if (love) {
        if (love === team) {
            status = 'this';
        } else {
            console.log(love, team)
            status = 'other';
        }
    }
    console.log(status)


    switch (status) {
        case 'none':
            if (user !== false) {
                return (
                    <div onClick={
                        () => {
                            sendLoveTeam()
                        }
                    } style={{
                        background: 'grey',
                        position: 'absolute',
                        height: '50px',
                        width: '50px',
                        top: '2vh',
                        right: '3vw',
                        borderRadius: '50%',
                        lineHeight: '50px',
                        fontSize: '45px',
                        textAlign: 'center',
                        cursor: 'pointer',
                        animation: 'bounce 3s ease infinite alternate',
                    }}>
                        ❤
                </div>
                );
            } else {
                return false;
            }

        case 'this':
            return (
                <div onClick={
                    () => {
                        sendLoveTeam()
                    }
                } style={{
                    background: '#cc682d',
                    color: '#ffffff',
                    position: 'absolute',
                    height: '50px',
                    width: '50px',
                    top: '2vh',
                    right: '3vw',
                    borderRadius: '50%',
                    lineHeight: '50px',
                    fontSize: '45px',
                    textAlign: 'center',
                    cursor: 'pointer',
                }}>
                    ❤
                </div>
            );
        case 'other':
            return (
                <div onClick={
                    () => {
                        sendLoveTeam()
                    }
                } style={{
                    background: 'grey',
                    position: 'absolute',
                    height: '50px',
                    width: '50px',
                    top: '2vh',
                    right: '3vw',
                    borderRadius: '50%',
                    lineHeight: '50px',
                    fontSize: '45px',
                    textAlign: 'center',
                    cursor: 'pointer',
                }}>
                    ❤
                </div>
            );

        default:
            break;
    }


}

export default LoveTeam;