import React, { useState } from 'react';
import { Link, withRouter } from 'react-router-dom';
import cookie from 'react-cookies';

const axios = require('axios')

function LeaderBoard() {

    //https://api.overwatchleague.com/ranking

    const [rankingTeams, setRankingTeams] = useState(null);
    const [rankingPlayers, setRankingPlayers] = useState(null);

    if (rankingTeams === null && rankingPlayers === null) {
        axios.get('https://api.overwatchleague.com/ranking')
            .then(function (response) {

                const test = response.data.content.sort(function (a, b) {
                    return a.placement - b.placement
                })
                setRankingTeams(test);
            })
            .catch(function (error) {
                console.log(error);
            });
        axios.get('api/getPlayersRanking')
            .then(function (response) {
                setRankingPlayers(response.data);
            })
            .catch(function (error) {
                console.log(error);
            });


        return (
            <div>
                LeaderBoard
            </div>
        )
    } else {
        let PlayersRanking;
        let TeamRankings;
        if (rankingPlayers !== null && rankingTeams !== null) {
            let pos = 1;
            PlayersRanking = rankingPlayers.map((player, key) => {
                if (rankingPlayers[key - 1]) {
                    if (rankingPlayers[key - 1].score.total !== rankingPlayers[key].score.total) {
                        pos += 1;
                    }
                }
                return (<PlayerRanking player={player} key={player.name} pos={pos}></PlayerRanking>)
            })
            TeamRankings = rankingTeams.map((team) => {
                return (<TeamRanking team={team} key={team.competitor.id}></TeamRanking>)
            })
        }


        return (

            <div className="LeaderBoard" style={{
                padding: '10px auto',
                display: 'flex',
                flexWrap: 'wrap',
                justifyContent: 'center',
                alignItems: 'start',
            }}>
                <div className="LeaderBoardTeams" style={{
                    width: '300px',
                    // marginRight: '2vw',
                    margin: '0 auto',
                    fontSize: '1.2em',
                }}>
                    <h3>Teams</h3>
                    <div className="teamRanking" style={{
                        display: 'flex',
                        width: '100%',
                        justifyContent: 'space-between',
                        alignItems: 'center',
                        borderBottom: '2px solid #cc682d',
                    }}>
                        <div className="placement" style={{
                            height: '30px',
                            width: '10px',
                        }}>#</div>
                        <div className="img" style={{
                            height: '30px',
                            width: '30px',
                        }}></div>
                        <div className="name" style={{
                            height: '30px',
                            width: '150px',
                        }}>Equipe</div>

                        <div className="win" style={{
                            width: '30px',
                            textAlign: 'center',
                        }}>+/-</div>
                        <div className="win" style={{
                            width: '30px',
                            textAlign: 'center',
                        }}>W</div>
                        <div className="lose" style={{
                            width: '30px',
                            textAlign: 'center',
                        }}>L</div>



                    </div>
                    {TeamRankings}
                </div>
                <div className="LeaderBoardPlayers" style={{
                    width: '360px',
                    fontSize: '1.2em',
                    margin: '0 auto',
                    // marginRight: '2vw',
                }}>
                    <h3>Players</h3>
                    <div className="playerRanking" style={{
                        display: 'inline-flex',
                        // width: '100%',
                        borderBottom: '2px solid #cc682d',

                    }}>
                        <div className="placement" style={{
                            height: '30px',
                            width: '10px',
                        }}>#</div>
                        <div className="loveTeam" style={{
                            height: '30px',
                            width: '30px',
                            lineHeight: '30px',
                            textAlign: 'center',
                        }}>❤</div>
                        <div className="name" style={{
                            height: '30px',
                            width: '80px',
                            lineHeight: '30px',
                        }}>Pseudo</div>
                        <div className="score" style={{
                            height: '30px',
                            width: '30px',
                            lineHeight: '30px',
                        }}>win</div>
                        <div className="score" style={{
                            height: '30px',
                            width: '40px',
                            lineHeight: '30px',
                        }}>lose</div>
                        {/* <div className="score">Stage 1</div> */}
                        <div className="score" style={{
                            height: '30px',
                            width: '30px',
                            lineHeight: '30px',
                        }}>S - 1</div>
                        <div className="score" style={{
                            height: '30px',
                            width: '30px',
                            lineHeight: '30px',
                        }}>S - 2</div>
                        <div className="score" style={{
                            height: '30px',
                            width: '30px',
                            lineHeight: '30px',
                        }}>S - 3</div>
                        <div className="score" style={{
                            height: '30px',
                            width: '30px',
                            lineHeight: '30px',
                        }}>S - 4</div>
                        <div className="score" style={{
                            height: '30px',
                            width: '30px',
                            lineHeight: '30px',
                        }}>Total</div>
                    </div>
                    {PlayersRanking}
                </div>


            </div>

        )
    }
}


function TeamRanking({ team }) {

    const statDiff = team.records[0].comparisons.find((value) => {
        if (value.key === "MATCH_GAME_DIFFERENTIAL") {
            return value.value;
        }
        return null;
    })
    const test = (statDiff && statDiff.value && statDiff.value > 0) ? {
        color: 'green'
    } : {
            color: 'red'
        };

    return (
        <div className="teamRanking" style={{
            display: 'inline-flex',
            width: '100%',
            justifyContent: 'start',
            alignItems: 'center',
            borderBottom: '2px solid #cc682d',
        }}>
            <div className="placement" style={{
                height: '30px',
                width: '10px',
            }}>{team.placement}</div>
            <div className="img" style={{
                background: `center/70% url("imgs/teams/${team.competitor.abbreviatedName}.png") no-repeat`,
                height: '30px',
                width: '30px',
            }}></div>
            <div className="name" style={{
                height: '30px',
                width: '170px',
                lineHeight: '30px',
            }}>{team.competitor.name}</div>

            <div className="win" style={{
                ...test,
                width: '30px',
                textAlign: 'center',
            }}>{(statDiff) ? statDiff.value : ''}</div>
            <div className="win" style={{
                width: '30px',
                textAlign: 'center',
            }}>{team.records[0].matchWin}</div>
            {/* <div className="draw" style={{
                width: '30px',
                textAlign: 'center',
            }}>{team.records[0].matchDraw}</div> */}
            <div className="lose" style={{
                width: '30px',
                textAlign: 'center',
            }}>{team.records[0].matchLoss}</div>
        </div>
    )
}

function PlayerRanking({ player, pos }) {
    if (player.status >= 2 && player.status !== 9 && player.score.total > 0) {
        return (
            <div className="playerRanking" style={{
                display: 'inline-flex',
                borderBottom: '2px solid #cc682d',
                // flexWrap: 'wrap',
                

            }}>
                <div className="placement" style={{
                    height: '30px',
                    width: '10px',
                }}>{pos}</div>
                <div className="loveTeam" style={{
                    height: '30px',
                    width: '30px',
                    background: `center/70% url("imgs/teams/${player.loveTeam}.png") no-repeat`
                }}>
                </div>
                <PlayerName player={player}></PlayerName>
                {/* <div className="score">{player.score.totalBets}</div> */}
                <div className="score" style={{
                    height: '30px',
                    width: '30px',
                    lineHeight: '30px',
                }}>{player.score.win}</div>
                <div className="score" style={{
                    height: '30px',
                    width: '40px',
                    lineHeight: '30px',
                }}>{player.score.lose}</div>
                {/* <div className="score">{player.score.stages[0].total}</div> */}
                <div className="score" style={{
                    height: '30px',
                    width: '30px',
                    lineHeight: '30px',
                    textAlign: 'center',
                }}>{player.score.stages[0].total}</div>

                <div className="score" style={{
                    height: '30px',
                    width: '30px',
                    lineHeight: '30px',
                    textAlign: 'center',
                }}>{player.score.stages[1].total}</div>
                <div className="score" style={{
                    height: '30px',
                    width: '30px',
                    lineHeight: '30px',
                    textAlign: 'center',
                }}>{player.score.stages[2].total}</div>
                <div className="score" style={{
                    height: '30px',
                    width: '30px',
                    lineHeight: '30px',
                    textAlign: 'center',
                }}>{player.score.stages[3].total}</div>

                <div className="score" style={{
                    height: '30px',
                    width: '30px',
                    lineHeight: '30px',
                    textAlign: 'center',
                }}>{player.score.total}</div>
            </div>
        );
    }
    else {
        return '';
    }

}
function PlayerName({ player }) {
    if (player.visibility || cookie.load('user').status > 2) {
        return (
            <div className="name" style={{
                width: '80px',
                height: '30px',
                lineHeight: '30px',
            }}>
                <Link to={`/profil/${player.login}`}>{player.name}</Link>
            </div>
        )
    } else {
        return (
            <div className="name" style={{
                width: '80px',
                height: '30px',
                lineHeight: '30px',
            }}>
                {player.name}
            </div>
        )
    }
}

export default withRouter(LeaderBoard);