import React, { useState } from 'react';
import { Link, withRouter } from 'react-router-dom';

const axios = require('axios');


function Teams() {
    const [teams, setTeams] = useState(null);
    if (teams === null) {
        axios.get('https://api.overwatchleague.com/teams?lang=fr')
            .then(function (response) {
                setTeams(response.data.competitors);
            })
            .catch(function (error) {
                console.log(error);
            });
        return (
            <div>
                <h2>Teams</h2>
            </div>
        );
    } else {
        const arrayTeams = teams.map((team) => {
            return (<Team team={team.competitor} key={team.competitor.id}></Team>)
        })
        return (<div className="Teams">
            <div style={{
                display: 'flex',
                flexWrap: 'wrap',
                alignItems: 'center',
                justifyContent: 'center',
                marginTop: '5vh',
            }}>
                {arrayTeams}
            </div>

        </div>)

    }
}

function Team({ team }) {
    return (
        <div style={{
            width: '150px',
            border: '2px solid #fff'
        }} onMouseEnter={({ currentTarget }) => {
            currentTarget.style['background'] = '#cc682d';
            currentTarget.style['color'] = '#ffffff';
            currentTarget.style['border'] = '2px solid #cc682d';
            currentTarget.querySelector('a').style['color'] = '#ffffff';
            currentTarget.querySelector('.img').style['borderBottom'] = '2px solid #ffffff';
        }}
        onMouseLeave={({ currentTarget }) => {
            currentTarget.style['background'] = '#ffffff';
            currentTarget.style['color'] = '#cc682d';
            currentTarget.style['border'] = '2px solid #ffffff';
            currentTarget.querySelector('a').style['color'] = '#cc682d';
            currentTarget.querySelector('.img').style['borderBottom'] = '2px solid #cc682d';
        }}>
            <Link to={`/team/${team.id}`}>
                {/* {team.abbreviatedName} */}
                <div className="img" style={{
                    background: `#ffffff center/50% url(${team.logo}) no-repeat`,
                    borderBottom: '2px solid #cc682d',
                    height: '100px',
                    width: '100%',
                    paddingBottom: '10px',
                }}></div>
                <div className="name" style={{
                    width: '100%',
                    textAlign: 'center',
                }}>{team.name}</div>
            </Link>
        </div>
    )
}


export default withRouter(Teams);