import React, { useState } from 'react';
import { withRouter } from 'react-router-dom';

import Achievement from '../../components/Achievement';
import UserBets from '../../components/UserBets';

const axios = require('axios');

function Profil({ match }) {



    const [userName, setUserName] = useState(match.params.user);
    const [user, setUser] = useState(null);

    if (user !== null && userName === match.params.user) {
        if (user.error) {
            return (
                <div className="Profil">
                    <h2>{user.login}</h2>
                </div>

            )
        }
        const style = {
            backgroundImage: `url("../imgs/teams/${user.loveTeam}.png")`
        };
        return (
            <div className="Profil">
                <div className="user">
                    <div className="loveTeam" style={style}>
                    </div>
                    <div className="bigName">
                    {user.name}
                    </div>
                </div>
                
                <h2>Haut Fait</h2>
                <Achievement user={user} />
                <h2>Pronostic</h2>
                {(user.bets) ? <UserBets user={user} /> : ''}

            </div>
        )

    }
    else if (userName !== match.params.user) {
        setUserName(match.params.user);

    }
    else {
        if (userName != null) {
            axios.get(`../api/getPlayer/${userName}`)
                .then(function (response) {
                    setUser(response.data);
                })
                .catch(function (error) {
                    console.log(error);
                });
        }

        return (
            <div className="Profil">
                <h2>{match.params.user}</h2>
            </div>
        );
    }


}



export default withRouter(Profil);